import { client } from '@/lib/sanity';
import { notFound } from 'next/navigation';

async function getArticle(slug) {
  return await client.fetch(
    `*[_type in ["article", "articles"] && slug.current == $slug][0] {
      title,
      excerpt,
      "slug": slug.current,
      "image": image.asset->url,
      body,
      category,
      author,
      publishedAt
    }`,
    { slug }
  );
}

// ðŸŽ¯ Meta tags dinÃ¢micas (OG tags)
export async function generateMetadata({ params }) {
  const article = await getArticle(params.slug);
  
  if (!article) return {};

  return {
    title: article.title,
    description: article.excerpt,
    openGraph: {
      title: article.title,
      description: article.excerpt,
      images: [
        {
          url: article.image || '/og-default.jpg',
          width: 1200,
          height: 630,
          alt: article.title,
        }
      ],
      type: 'article',
    },
    twitter: {
      card: 'summary_large_image',
      title: article.title,
      description: article.excerpt,
      images: [article.image || '/og-default.jpg'],
    },
  };
}

export default async function ArticlePage({ params }) {
  const article = await getArticle(params.slug);

  if (!article) {
    notFound();
  }

  return (
    <article className="container mx-auto px-4 py-8 max-w-4xl">
      {article.image && (
        <img 
          src={article.image} 
          alt={article.title} 
          className="w-full h-96 object-cover rounded-lg mb-8"
        />
      )}
      <h1 className="text-4xl font-bold mb-4">{article.title}</h1>
      <p className="text-gray-600 mb-8">{article.excerpt}</p>
      
      <div className="flex gap-4 text-sm text-gray-500 mb-8">
        {article.author && <span>Por {article.author}</span>}
        {article.publishedAt && (
          <span>{new Date(article.publishedAt).toLocaleDateString('pt-BR')}</span>
        )}
        {article.category && <span className="bg-blue-100 px-2 py-1 rounded">{article.category}</span>}
      </div>

      <div className="prose max-w-none">
        {/* Aqui vocÃª renderiza o body - se for Portable Text precisa de um componente especÃ­fico */}
        {article.body}
      </div>
    </article>
  );
}
